<template>
<div>
  <h1>Welcome {{user}}</h1>
  <p><router-link :to="{name: 'invoices'}">Invoices</router-link></p>
  <button @click="logOut" class="btn"> Log out</button>
</div>
</template>

<script>
import store from "@/store";
export default {
    data(){
        return{user:store.user}; 
    },
    methods:{
        logOut(){
            store.user = null;
            this.$router.push("/");
        }
    }
}
</script>

